.form-wrapper {
  flex-shrink: 0;
  width: 570px;
  min-width: 320px;
  padding: 30px 0 30px 0;

  @media (max-width: $desktop_breakpoint) {
    width: 591px;
  }

  @media (max-width: $tablet_breakpoint) {
    width: 100%;
    padding: 29px 15px 33px 15px;
  }
}

.form-wrapper h3 {
  margin: 0 0 10px -5px;

  @include font_robotcon(36px, 51px, 400, $basic_light);

  @media (max-width: $desktop_breakpoint) {
    margin: 0 0 16px -2px;

    font-size: 32px;
    line-height: 45px;
  }

  @media (max-width: $tablet_breakpoint) {
    margin: 0 0 13px 0;

    font-size: 28px;
    line-height: 39px;
  }
}

.form-wrapper p {
  margin: 0 0 50px 0;

  @include font_ptmono(16px, 22px, 400, $basic_light);


  @media (max-width: $desktop_breakpoint) {
    margin: 0 0 52px 3px;
  }

  @media (max-width: $tablet_breakpoint) {
    margin: 0 0 26px 0;
  }
}


.form-wrapper__title {
  //
}

.form-wrapper__subtitle {
  //
}


.form-wrapper__modal-title,
.form-wrapper__modal-subtitle {
  display: none;
}


.form-wrapper__modal-close {
  position: absolute;
  top: 19px;
  right: 14px;

  display: none;
  min-width: 20px;
  min-height: 20px;

  background-color: transparent;
  border: none;

  & svg {
    width: 17px;
    height: 17px;

    fill: $basic_light;
  }
}
